.page-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 98px;
  padding-left: 30px;
  padding-right: 30px;

  @media (max-width: $tablet-max-width) {
    padding-left: 21px;
    padding-right: 21px;
    padding-bottom: 53px;
  }

  @media (max-width: $mobile-max-width) {
    display: block;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 51px;
  }

  h2 {
    margin-top: 0;
    margin-bottom: 40px;
    font-weight: 600;
    font-size: 30px;
    line-height: 30px;
    letter-spacing: -0.005em;
    color: $black;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 20px;
      font-size: 24px;
      line-height: 41px;
    }

    @media (max-width: $mobile-max-width) {
      text-align: center;
      font-size: 18px;
    }
  }

  p {
    width: 38%;
    margin-top: 0;
    margin-bottom: 40px;
    font-size: 17px;
    line-height: 31px;
    text-align: center;
    color: rgba($basic-black, 0.7);

    @media (max-width: $tablet-max-width) {
      width: 100%;
      margin-bottom: 20px;
    }

    @media (max-width: $mobile-max-width) {
      margin-bottom: 25px;
      text-align: center;
    }
  }

  img {
    width: 957px;
    height: 530px;
    margin-bottom: 33px;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 20px;
    }

    @media (max-width: $mobile-max-width) {
      width: 100%;
      object-fit: cover;
      margin-bottom: 31px;
    }
  }
}

.page-footer__contacts {
  display: flex;
  font-size: 16px;
  line-height: 22px;
  color: rgba($basic-black, 0.5);

  @media (max-width: $mobile-max-width) {
    justify-content: center;
  }
}

.page-footer__link {
  &:not(:last-child) {
    margin-right: 76px;
  }

  svg {
    margin-right: 36px;

    @media (max-width: $tablet-max-width) {
      margin-right: 15px;
    }
  }
}
